import React, {Component} from "react";

import { TextInputGroup } from "./inputs";


/*eslint no-unused-vars: "off" */
export const COMMON_FIELDS = [
  {
    name: "title",
    label: "Ttulo",
    placeholder: "Ex.: Veja histrico da taxa de analfabetismo no brasil"
  },
  {
    name: "subtitle",
    label: "Subttulo",
    placeholder: "Ex.: ndice no apresentava um aumento desde 1997"
  },
  {
    name: "credits",
    label: "Fonte",
    placeholder: "Ex.: IBGE"
  }
];

export default function CommonFieldsGroup(fields, model, onChange) {
  return (
    <div>
    {fields.map((field) => {
      return (
        <TextInputGroup
        key={field.name}
        name={field.name}
        label={field.label}
        placeholder={field.placeholder}
        onChange={onChange}
        defaultValue={model[field.name]}
        />
      );
    })}
    </div>
  );
}



